var app = this;


console.log(pos);
console.log(file);

var params = new FormData();
params.append('smfile', file);
axios.post('https://sm.ms/api/upload',params,{



headers: {
'Content-Type': 'multipart/from-data'
}
}).then(function(response){



app.$refs.md.$img2Url(pos, response.data.data.url);
});
},

addtopic() {
console.log('')













}
}

}



